import React, { Component } from 'react'
import Logout from './Logout'
import Profile from './Profile'
import Proficiency from './Proficiency'
import Reminders from './Reminders'

export class RightSidebar extends Component {
  render() {
    const className = 'right-sidebar'
    const contentClass = `${className}__content`
    return (
      <aside className={className}>
        <div className={contentClass}>
          <div>
            <Logout parentClass={contentClass} />
            <Profile parentClass={contentClass} />
            <Proficiency parentClass={contentClass} />
          </div>
          <Reminders parentClass={contentClass} />
        </div>
      </aside>
    )
  }
}

export default RightSidebar
