import { getDates } from './utils/range'

const areas = ['Writing', 'Reading', 'Coding']

const getActivityForDate = (activity, date) => {
  const dateActivity = areas.map((area) => ({
    date,
    time: Math.random() * 10,
    area,
  }))
  return [...activity, ...dateActivity]
}

const getActivity = () => {
  const dates = getDates()
  return dates.reduce(getActivityForDate, [])
}

