import React, { Component } from 'react'
import { extend } from '../../../../../utils/classes'
import Graph from './Graph'

export class DayGraph extends Component {
  render() {
    const { date, parentClass, activity } = this.props
    const dayName = date.toLocaleString('en-US', { weekday: 'short' })
    const baseClass = extend(parentClass, 'item')
    return (
      <div className={baseClass}>
        <span className={extend(baseClass, 'day')}>{dayName}</span>
        <Graph parentClass={baseClass} activity={activity} />
      </div>
    )
  }
}

export default DayGraph
