import React, { Component } from 'react'
import { ReactComponent as RightArrow } from '../../../icons/arrow-right.svg'

export class Toolbar extends Component {
  render() {
    const { parentClass } = this.props
    const baseClass = `${parentClass}__toolbar`
    const moreClass = `${baseClass}__more`
    return (
      <div className={baseClass}>
        <h3 className={`${baseClass}__heading`}>Latest Results</h3>
        <button className={moreClass}>
          More
          <RightArrow className={`${moreClass}__icon`} />
        </button>
      </div>
    )
  }
}

export default Toolbar
