import React, { Component } from 'react'
import Progress from './Progress'

export class Item extends Component {
  render() {
    const { parentClass, course, unit, result } = this.props
    const baseClass = `${parentClass}__item`
    return (
      <div className={baseClass}>
        <div>
          <span className={`${baseClass}__unit`}>Unit {unit.id + 1} - </span>
          <span className={`${baseClass}__course`}>{course.name}</span>
        </div>
        <Progress result={result} parentClass={baseClass} />
      </div>
    )
  }
}

export default Item
