import React, { Component } from 'react'
import { ReactComponent as NotificationIcon } from '../../../../icons/notification.svg'
import List from './List'
import { extend } from '../../../../utils/classes'

export class Reminders extends Component {
  render() {
    const { parentClass } = this.props
    const baseClass = `${parentClass}__reminders`
    const toolbarClass = `${baseClass}__toolbar`
    return (
      <div className={baseClass}>
        <div className={toolbarClass}>
          <h5 className={extend(toolbarClass, 'title')}>Reminders</h5>
          <NotificationIcon className={`${toolbarClass}__icon`} />
        </div>
        <List parentClass={baseClass} />
      </div>
    )
  }
}

export default Reminders
