import React, { Component } from 'react'
import data from '../../../../data'
import Item from './Item'
import { getCourseWithResults } from '../../../../utils/courses'

export class Proficiency extends Component {
  getBaseClass = () => {
    const { parentClass } = this.props
    return `${parentClass}__proficiency`
  }

  baseClass = this.getBaseClass()

  sortResultsByScores = () =>
    data.user.results.sort((result1, result2) => result1.score - result2.score)

  getTopResults = () => {
    const sortedResults = this.sortResultsByScores()
    return sortedResults.slice(0, 2)
  }

  getTopCourses = () => {
    const topResults = this.getTopResults()
    return topResults.map(getCourseWithResults)
  }

  renderTopCourses = () => {
    const topCourses = this.getTopCourses()
    return topCourses.map((courseAndResult, index) => (
      <Item {...courseAndResult} key={index} parentClass={this.baseClass} />
    ))
  }

  render() {
    return <div className={this.baseClass}>{this.renderTopCourses()}</div>
  }
}

export default Proficiency
