import React, { Component } from 'react'
import Progress from '../../../../../shared/Progress'
import Unit from '../../../../../shared/Unit'

export class Item extends Component {
  getLevel = () => {
    const { score } = this.props.result
    if (score <= 0.7) {
      return 'Beginner'
    } else if (score <= 0.85) {
      return 'Proficient'
    } else if (score <= 0.9) {
      return 'Advanced'
    }
    return 'Expert'
  }

  render() {
    const { course, result, parentClass } = this.props
    const baseClass = `${parentClass}__item`
    return (
      <div className={baseClass}>
        <Unit parentClass={baseClass} course={course} unitId={result.unit} />
        <div>
          <h2 className={`${baseClass}__heading`}>{course.name}</h2>
          <span className={`${baseClass}__level`}>{this.getLevel()}</span>
        </div>
        <Progress parentClass={baseClass} percentage={result.score * 100} />
      </div>
    )
  }
}

export default Item
