import React, { Component } from 'react'
import Item from './Item'
import { ReactComponent as Dashboard } from '../../../icons/dashboard.svg'
import { ReactComponent as classes } from '../../../icons/classes.svg'
import { ReactComponent as resources } from '../../../icons/resources.svg'
import { ReactComponent as plan } from '../../../icons/plan.svg'
import { ReactComponent as chat } from '../../../icons/chat.svg'
import { ReactComponent as settings } from '../../../icons/settings.svg'

export class Links extends Component {
  links = [
    {
      children: 'Dashboard',
      to: '/',
      Icon: Dashboard,
    },
    { children: 'Courses', to: '/courses', Icon: classes },
    { children: 'Resources', to: '/resources', Icon: resources },
    { children: 'Plan', to: '/plan', Icon: plan },
    { children: 'Chat', to: '/chat', Icon: chat },
    { children: 'Settings', to: '/settings', Icon: settings },
  ]

  baseClass = 'links'

  renderLinks = () => {
    return this.links.map((linkData, index) => (
      <Item {...linkData} key={index} parentClass={this.baseClass} />
    ))
  }

  render() {
    return <ul className={this.baseClass}>{this.renderLinks()}</ul>
  }
}

export default Links
