import React, { Component } from 'react'
import { extendParentClass, extend } from '../../../../utils/classes'
import Unit from '../../../../shared/Unit'
import { ReactComponent as ArrowRight } from '../../../../icons/arrow-right.svg'

export class Item extends Component {
  baseClass = extendParentClass.bind(this)('item')

  render() {
    const { units, name } = this.props
    const [unit] = units
    const body = extend(this.baseClass, 'body')
    const header = extend(body, 'header')
    return (
      <button className={this.baseClass}>
        <Unit parentClass={this.baseClass} unitId={unit.id} />
        <div className={body}>
          <span className={extend(body, 'course')}>{name}</span>
          <div className={header}>
            <h3 className={extend(header, 'title')}>{unit.name}</h3>
            <ArrowRight />
          </div>
        </div>
      </button>
    )
  }
}

export default Item
