import React, { Component } from 'react'

export class Unit extends Component {
  getUnitAsLetter = () => {
    const { unitId } = this.props
    return String.fromCharCode(unitId + 97)
  }

  render() {
    const { unitId, parentClass } = this.props

    return (
      <div className={`${parentClass}__unit`}>
        {this.getUnitAsLetter()}
        {unitId}
      </div>
    )
  }
}

export default Unit
