import { ReactComponent as Envelope } from '../../../../../icons/envelope.svg'
import { ReactComponent as Alert } from '../../../../../icons/alert-circle.svg'
import React, { Component } from 'react'

export class Icon extends Component {
  getIconComponent = () => {
    const { type } = this.props
    if (type === 'test') {
      return Alert
    }
    return Envelope
  }

  getBaseClassAndType = (className) =>
    `${className} ${className}--${this.props.type}`

  render() {
    const { parentClass } = this.props
    const baseClass = `${parentClass}__icon`
    const IconComponent = this.getIconComponent()
    const svgClass = `${baseClass}__svg`
    return (
      <div className={this.getBaseClassAndType(baseClass)}>
        <IconComponent className={this.getBaseClassAndType(svgClass)} />
      </div>
    )
  }
}

export default Icon
