import React, { Component } from 'react'
import { ReactComponent as Icon } from '../../../../images/profile.svg'
import data from '../../../../data'

export class Profile extends Component {
  render() {
    const { parentClass } = this.props
    const profileClass = `${parentClass}__profile`
    const bodyClass = `${profileClass}__body`
    return (
      <div className={profileClass}>
        <Icon className={`${profileClass}__icon`} />
        <div className={bodyClass}>
          <h4 className={`${bodyClass}__heading`}>{data.user.name}</h4>
          <span className={`${bodyClass}__type`}>Student</span>
        </div>
      </div>
    )
  }
}

export default Profile
