import React, { Component } from 'react'
import { getCourseWithResults } from '../../../../utils/courses'
import data from '../../../../data'
import Item from './Item/index'

export class List extends Component {
  findUnitById = (course, unit) => course.units.find(({ id }) => id === unit)

  getResultWithUnit = (result) => {
    const resultWithCourse = getCourseWithResults(result)
    const unit = this.findUnitById(resultWithCourse.course, result.unit)
    return {
      ...resultWithCourse,
      unit,
    }
  }

  getResultsWithUnits = () => {
    const { results } = data.user
    return results.map(this.getResultWithUnit)
  }

  renderResults = () => {
    const results = this.getResultsWithUnits()
    const { parentClass } = this.props
    const baseClass = `${parentClass}__list`
    return results.map((result, index) => (
      <Item {...result} key={index} parentClass={baseClass} />
    ))
  }

  render() {
    return this.renderResults()
  }
}

export default List
