import React, { Component } from 'react'
import DayGraph from './DayGraph'
import data from '../../../../data'
import { extend } from '../../../../utils/classes'
import { getDates } from '../../../../utils/range'

export class Graph extends Component {
  baseClass = extend(this.props.parentClass, 'graphs')

  getActivityForDay = (date) =>
    data.user.activity.filter(
      ({ date: currentDate }) => currentDate.getDate() === date.getDate()
    )

  renderDayGraph = (date, index) => {
    const activity = this.getActivityForDay(date)
    return (
      <DayGraph
        date={date}
        key={index}
        activity={activity}
        parentClass={this.baseClass}
      />
    )
  }

  renderDayGraphs = () => {
    const dates = getDates()
    return dates.map(this.renderDayGraph)
  }

  render() {
    return <div className={this.baseClass}>{this.renderDayGraphs()}</div>
  }
}

export default Graph
