import React, { Component } from 'react'
import { extend, getClassWithVariant } from '../../../../utils/classes'
import data from '../../../../data'

export class Legend extends Component {
  baseClass = extend(this.props.parentClass, 'legend')

  renderItem = (area, index) => {
    const baseClass = extend(this.baseClass, 'item')
    const className = getClassWithVariant(area, baseClass)
    return (
      <span className={className} key={index}>
        {area}
      </span>
    )
  }

  renderItems = () => data.areas.map(this.renderItem)

  render() {
    return <div className={this.baseClass}>{this.renderItems()}</div>
  }
}

export default Legend
