import React, { Component } from 'react'
import Icon from './Icon'
import { formatDate } from '../../../../../utils/formatDate'

export class Item extends Component {
  getDate = () => formatDate(this.props.date)

  render() {
    const { courseName, name, parentClass, type } = this.props
    const baseClass = `${parentClass}__item`
    const bodyClass = `${baseClass}__body`
    return (
      <div className={baseClass}>
        <Icon type={type} parentClass={baseClass} />
        <div className={bodyClass}>
          <h6
            className={`${bodyClass}__heading`}
          >{`${courseName} - ${name}`}</h6>
          <span className={`${bodyClass}__date`}>{this.getDate()}</span>
        </div>
      </div>
    )
  }
}

export default Item
