import React from 'react'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import Dashboard from './Dashboard'
import Layout from './Layout'

function App() {
  return (
    <Router>
      <Layout>
        <Switch>
          <Route to="/" exact component={Dashboard} />
        </Switch>
      </Layout>
    </Router>
  )
}

export default App
