import React, { Component } from 'react'
import Item from './Item'
import data from '../../../../../data'

export class List extends Component {
  getBaseClass = () => {
    const { parentClass } = this.props
    return `${parentClass}__list`
  }

  baseClass = this.getBaseClass()

  getReminderWithCourseName = (course, unit) => {
    return unit.assignments.map((assignment) => ({
      ...assignment,
      courseName: course.name,
    }))
  }

  getRemindersFromUnits = (course) =>
    course.units.reduce((reminders, unit) => {
      const assignmentsWithUnit = this.getReminderWithCourseName(course, unit)
      return [...reminders, ...assignmentsWithUnit]
    }, [])

  getRemindersWithCourses = () =>
    data.courses.reduce((reminders, course) => {
      const courseReminders = this.getRemindersFromUnits(course)
      return [...reminders, ...courseReminders]
    }, [])

  renderItems = () => {
    const reminders = this.getRemindersWithCourses()
    return reminders.map((reminder, index) => (
      <Item {...reminder} key={index} parentClass={this.baseClass} />
    ))
  }

  render() {
    return <div className={this.baseClass}>{this.renderItems()}</div>
  }
}

export default List
