import React, { Component } from 'react'
import Links from '../Links'
import Toggle from './Toggle'

export class Nav extends Component {
  render() {
    const navId = 'nav'
    const bodyId = `${navId}__body`
    return (
      <div className="fixed-top navbar-light bg-light py-3" id={navId}>
        <div className="container">
          <Toggle navId={bodyId} />
          <div id={bodyId}>
            <Links />
          </div>
        </div>
      </div>
    )
  }
}

export default Nav
