import React, { Component } from 'react'
import BaseProgress from '../../../../../shared/Progress'
import { getClassWithVariant } from '../../../../../utils/classes'

export class Progress extends Component {
  baseClass = `${this.props.parentClass}__score`
  percentageClass = `${this.baseClass}__percentage`

  getVariant = () => {
    const { score } = this.props.result
    if (score >= 0.5) {
      return 'advanced'
    }
    return 'beginner'
  }

  getPercentageClass = () => {
    const variant = this.getVariant()
    return getClassWithVariant(variant, this.percentageClass)
  }

  render() {
    const { result } = this.props
    const percentage = result.score * 100
    return (
      <div className={this.baseClass}>
        <BaseProgress
          percentage={percentage}
          parentClass={this.baseClass}
          variant={this.getVariant()}
        />
        <span className={this.getPercentageClass()}>{percentage}%</span>
      </div>
    )
  }
}

export default Progress
