import React, { Component } from 'react'
import { extend } from '../../../utils/classes'
import { ReactComponent as ArrowDown } from '../../../icons/arrow-down.svg'

export class Toolbar extends Component {
  render() {
    const { parentClass } = this.props
    const baseClass = extend(parentClass, 'toolbar')
    const buttonClass = extend(baseClass, 'more')
    return (
      <div className={baseClass}>
        <h3 className={extend(baseClass, 'heading')}>Learning time</h3>
        <button className={buttonClass}>
          Last week
          <ArrowDown className={extend(buttonClass, 'icon')} />
        </button>
      </div>
    )
  }
}

export default Toolbar
