const getLastSunday = () => {
  const today = new Date()
  const daysFromSunday = today.getDay()
  today.setDate(today.getDate() - daysFromSunday)
  return today
}

const getStartDate = () => {
  const today = new Date()
  if (today.getDay() === 0) {
    return today
  }
  return getLastSunday()
}

export const getDates = () => {
  const startDate = getStartDate()
  let dates = []
  for (let dayIndex = 0; dayIndex < 7; dayIndex++) {
    const date = new Date()
    date.setDate(startDate.getDate() + dayIndex)
    dates.push(date)
  }
  return dates
}
