import React, { Component } from 'react'
import { extend } from '../utils/classes'

export class Toolbar extends Component {
  render() {
    const { parentClass, title, action, Icon } = this.props
    const baseClass = extend(parentClass, 'toolbar')
    const buttonClass = extend(baseClass, 'more')
    return (
      <div className={baseClass}>
        <h3 className={extend(baseClass, 'heading')}>{title}</h3>
        <button className={buttonClass}>
          {action}
          <Icon className={extend(buttonClass, 'icon')} />
        </button>
      </div>
    )
  }
}

export default Toolbar
