import React, { Component } from 'react'
import Item from './Item'
import { extendParentClass } from '../../../../utils/classes'
import data from '../../../../data'

export class List extends Component {
  getBaseClass = () => {
    return extendParentClass.bind(this)('list')
  }

  baseClass = this.getBaseClass()

  getTopCourses = () => data.courses.slice(0, 3)

  renderItems = () => {
    const topCourses = this.getTopCourses()
    return topCourses.map((course, index) => (
      <Item key={index} {...course} parentClass={this.baseClass} />
    ))
  }

  render() {
    return <div className={this.baseClass}>{this.renderItems()}</div>
  }
}

export default List
