import React, { Component } from 'react'
import { extend, getClassWithVariant } from '../../../../../utils/classes'
import data from '../../../../../data'

export class Graph extends Component {
  baseClass = extend(this.props.parentClass, 'graph')

  getTimeForArea = (area) => {
    const { activity } = this.props
    return activity.reduce((time, activity) => {
      if (activity.area === area) {
        return activity.time + time
      }
      return time
    }, 0)
  }

  getTotalTime = () =>
    this.props.activity.reduce((total, activity) => total + activity.time, 0)

  totalTime = this.getTotalTime()

  getPercentOfTime = (area) => {
    const timeForArea = this.getTimeForArea(area)
    return (timeForArea / this.totalTime) * 100
  }

  getBarClass = (area) => {
    const barClass = extend(this.baseClass, 'bar')
    return getClassWithVariant(area, barClass)
  }

  renderBar = (area, index) => {
    const percentage = this.getPercentOfTime(area)
    return (
      <div
        className={this.getBarClass(area)}
        style={{ height: `${percentage}%` }}
        key={index}
        aria-valuenow={percentage}
        aria-valuemin="0"
        aria-valuemax="100"
      />
    )
  }

  renderBars = () => {
    return data.areas.map(this.renderBar)
  }

  render() {
    return <div className={this.baseClass}>{this.renderBars()}</div>
  }
}

export default Graph
