import React, { Component } from 'react'
import { getClassWithVariant } from '../utils/classes'

export class Progress extends Component {
  getClassWithVariant = (baseClass) => {
    const { variant } = this.props
    return getClassWithVariant(variant, baseClass)
  }

  render() {
    const { percentage, parentClass } = this.props
    const progressClass = `${parentClass}__progress`
    const barClass = this.getClassWithVariant(`${progressClass}__bar`)
    return (
      <div className={`progress ${progressClass}`}>
        <div
          className={`progress-bar ${barClass}`}
          role="progressbar"
          style={{ width: `${percentage}%` }}
          aria-valuenow={percentage}
          aria-valuemin="0"
          aria-valuemax="100"
        ></div>
      </div>
    )
  }
}

export default Progress
